import {Location, SectionData} from '@typings/db';

export function convertLocationToString(location: Location) {
  if (location == Location.FIRST_GATE) {
    return 'first gate';
  } else if (location == Location.LAST_GATE) {
    return 'last gate';
  } else if (location == Location.SELECTED_POINT) {
    return 'selected point';
  } else if (location == Location.CURRENT_POINT) {
    return 'current point';
  } else if (location == Location.PREVIOUS_GATE_BASED_ON_SELECTED) {
    return 'previous gate by selected';
  } else if (location == Location.LATEST_GATE_BASED_ON_SELECTED) {
    return 'latest gate by selected';
  } else if (location == Location.NEXT_GATE_BASED_ON_SELECTED) {
    return 'next gate by selected';
  } else if (location == Location.PREVIOUS_GATE_BASED_ON_CURRENT) {
    return 'previous gate by current';
  } else if (location == Location.LATEST_GATE_BASED_ON_CURRENT) {
    return 'latest gate by current';
  } else if (location == Location.NEXT_GATE_BASED_ON_CURRENT) {
    return 'next gate by current';
  }
}

export function convertSectionDataToString(sectionData: SectionData) {
  if (sectionData == SectionData.DISTANCE) {
    return 'Distance';
  } else if (sectionData == SectionData.ELAPSED_TIME) {
    return 'Time Taken';
  } else if (sectionData == SectionData.ESTIMATED_TIME) {
    return 'Expected Time';
  } else if (sectionData == SectionData.ELAPSED_SPEED) {
    return 'Average Speed';
  } else if (sectionData == SectionData.ESTIMATED_SPEED) {
    return 'Expected Speed';
  }
}
